.fade-list-enter-active,.fade-list-leave-active{transform:translate3d(30px, 0, 0)}.fade-list-enter,.fade-list-leave-to{transition:all .3s ease;opacity:0}.col-table-container table{border-collapse:collapse;border:1px solid #f5f5f5;table-layout:fixed;width:100%}.col-table-container table td{color:#8c8a8a;border-left:1px solid #f5f5f5;border-bottom:1px solid #f5f5f5;box-sizing:border-box;text-overflow:ellipsis;vertical-align:middle;position:relative}.col-table-container .col-tr-one{background:#EEF2F8}
